import mongoose from "mongoose";
const Schema = mongoose.Schema;

const InvoiceSchema = new Schema({
  item: {
    type: String,
    required: true
  },
  qty: {
    type: Number,
    required: true
  },
  date: {
    type: Date,
    required: true,
    default: Date.now
  },
  due: {
    type: Date,
    required: true,
    default: Date.now
  },
  rate: {
    type: Number
  },
  tax: {
    type: Number
  }
});

export default mongoose.model("Invoice", InvoiceSchema);
